@import url(https://fonts.googleapis.com/css?family=Abel|Roboto:100,300,400|Roboto+Condensed:300,400|Oswald:300,400&display=swap);
/* Font Import */
* {
  box-sizing: border-box; }

html, body, #root, .App {
  height: 100%; }

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center; }

.AppBtn {
  display: flex;
  cursor: pointer;
  width: 100%;
  background: #2196F3;
  color: #ffff;
  font-weight: 100;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  padding: 6px 16px;
  font-size: 0.875rem;
  min-width: 64px;
  line-height: 1.75;
  border-radius: 4px;
  letter-spacing: 0.02857em;
  text-transform: uppercase;
  margin: auto;
  max-width: 250px;
  text-align: center;
  outline: none;
  border: none; }

.modal {
  width: 100%;
  height: 100%;
  display: flex;
  background: #231f20bd; }

.Shade {
  margin: auto;
  outline: none;
  cursor: pointer; }

.Shade > canvas {
  border-radius: 3px;
  box-shadow: 0px 2px 9px 0px #00000094;
  border: solid 1px #404040; }

h2 {
  text-transform: uppercase;
  margin: 0; }

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear; } }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.ScreenLoader {
  z-index: 9999;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  background: #231f20;
  background: linear-gradient(45deg, #231f20 0%, #424242 50%, #231f20 100%); }
  .ScreenLoader .Loading-logo {
    -webkit-animation: Loading-logo-spin infinite 1s ease-in-out;
            animation: Loading-logo-spin infinite 1s ease-in-out;
    height: 25vmin;
    pointer-events: none;
    color: #fff; }

@-webkit-keyframes Loading-logo-spin {
  0% {
    -webkit-transform: perspective(500px) rotateY(0deg);
            transform: perspective(500px) rotateY(0deg); }
  100% {
    -webkit-transform: perspective(500px) rotateY(360deg);
            transform: perspective(500px) rotateY(360deg); } }

@keyframes Loading-logo-spin {
  0% {
    -webkit-transform: perspective(500px) rotateY(0deg);
            transform: perspective(500px) rotateY(0deg); }
  100% {
    -webkit-transform: perspective(500px) rotateY(360deg);
            transform: perspective(500px) rotateY(360deg); } }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

